* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  text-align: center;
  margin-top: 20px;
  display: flex;
}

.value {
  font-size: 40px;
}

.label {
  font-size: 20px;
}

.field {
  flex-direction: column;
  margin-right: 20px;
  display: flex;
}

.flatpickr-input[readonly] {
  width: 230px;
  height: 35px;
  font-size: 20px;
}

button[data-start], button[data-stop] {
  height: 35px;
  width: 60px;
  margin: 0;
  font-size: 20px;
}

